body {
    background-image: url('/src/back.jpg');
    background-size: cover;
    margin: 0;
    font-family: Arial, sans-serif;
}

.App {
    text-align: center;
    padding: 50px;
}

button {
    background-color: #1e3a5f;
    background-size: cover;
    border: none;
    padding: 15px 30px;
    margin: 10px;
    color: white;
    font-size: 18px;
    cursor: pointer;
    border-radius: 5px;
}

input {
    padding: 10px;
    font-size: 16px;
    margin: 20px;
    width: 200px;
}

p {
    font-size: 18px;
    color: white;
}
